import React, { useEffect, useState, useRef } from 'react';
import Navbar from './global-components/navbar-v2';
import ErrorPage from './section-components/error';
import Footer from './global-components/footer';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie, getElementAtEvent } from 'react-chartjs-2';
import Draggable, { DraggableCore } from "react-draggable";
import ChartDataLabels from "chartjs-plugin-datalabels";
import { getBaZhai, generateFiveYellow, generateArrow, generateGrandDuke, generateFlyingStar, generateMountain } from "./helpers/CompassConfig"
import { TWENTY_FOUR_MOUNTAIN } from '../cm-lib/constants/index'

ChartJS.register(ChartDataLabels);
ChartJS.register(ArcElement, Tooltip, Legend);

const Component = ({
  children,
  degree,
  handleClick,
  data,
  labels,
  backgroundColor,
  centerText = [],
  edgeText = [],
  fiveYellow,
  facing,
  period
}) => {

  const chartRef = useRef();
  const rotation = (degree - 157.5) * -1;
  const { bazhai, arcColor } = getBaZhai(facing);


  return <div className='overlayContainer'>
    <Draggable>
      <div className='overlay'>
        <Pie
          onContextMenu={e => handleClick(e, chartRef.current)}
          ref={chartRef}
          options={{
            rotation,
            plugins: {
              legend: {
                display: false
              },
              datalabels: {
                display: true,
                color: '#36A2EB'
              },
              tooltip: {
                enabled: false
              }
            }
          }}
          data={{
            labels,
            datasets: [{

              data,
              backgroundColor: arcColor,
              hoverOffset: 4,
              datalabels: {
                labels: {
                  ...generateFiveYellow(fiveYellow),
                  ...generateGrandDuke(),
                  //...generateFlyingStar('7', facing.facing),
                  ...generateMountain(facing.facing),
                  ...{ bazhai },
                  //...generateArrow(facing.facing),
                }
              }
            }]
          }}

        />
      </div>

    </Draggable>

    {children}
  </div>

}

export default Component
