import React from 'react'
import AdSense from 'react-adsense'
import { getAnnualStar, createBasePlate, getDirectionByIndex } from '../../cm-lib/XuanKongFlyingStar'
const Component = ({ onClick = () => {} }) => {
    const now = new Date()
    const annualStar = getAnnualStar(now)
    const periodPlate = createBasePlate(annualStar)
    const index = periodPlate.findIndex(e => e === 5)
    return <div>
        <button onClick={() => onClick(getDirectionByIndex(index))}>Show 5 Yellow { now.getFullYear() }</button>

    </div> 
}

export default Component




